<template>
	<div>
		<Loader v-if="isLoading" />
		<v-data-table
			v-else
			class="mt-6"
			:headers="header"
			:items="dataTingkat"
			:items-per-page="10">
			<template v-slot:no-data>
	      <v-alert type="info" class="mt-4">
	        <div>Data tidak ditemukan</div>
	      </v-alert>
	    </template>
	    <template v-slot:item.no="{item}">
	    	{{dataTingkat.map(x => x.id).indexOf(item.id)+1}}    	
	    </template>
			<template v-slot:item.action="{item}">
				<v-btn icon color="success">
					<v-icon small>mdi-eye</v-icon>
				</v-btn>
				<EditTingkat :data="item" />
				<HapusTingkat :id="item.id" />
			</template>		
		</v-data-table>
	</div>
</template>

<script>
	import { mapState } from 'vuex'
	import EditTingkat from '@/components/Prestasi/Tingkat/EditTingkat'
	import HapusTingkat from '@/components/Prestasi/Tingkat/HapusTingkat'
	import Loader from '@/components/Loader'

	export default{
		data: () => ({
			header: [
				{ text: 'No', value: 'no', sortable: false },
				{ text: 'Tingkat', value: 'nama', sortable: false },
				{ text: 'Action', value: 'action', align: 'center', sortable: false }
			],

			data: [
				{
					nama: 'Internasional',
					id: 1
				}
			],
		}),

		components: {
			Loader,
			HapusTingkat,
			EditTingkat
		},

		created() {
			this.$store.dispatch('dataTingkat/getDataTingkat')
			this.$store.dispatch('dataTingkat/updateIsLoading', true)
		},

		computed: { ...mapState('dataTingkat', ['dataTingkat', 'isLoading']) }
	}
</script>